@import "../../styles/lavenst.scss";

.calendar {
    user-select: none;

    //Month calendar  widget
    &.month-calendar-widget {
        display: flex;
        flex-direction: column;
        width: 100%;
        box-sizing: border-box;
        max-width: 280px;

        & .calendar-header-widget {
            width: 100%;

            & span {
                color: #70757a;
                -webkit-box-flex: 1;
                box-flex: 1;
                -webkit-flex-grow: 1;
                flex-grow: 1;
                padding-left: 5px;
                font-family: 'Google Sans',Roboto,Arial,sans-serif;
                font-size: 14px;
                font-weight: 500;
                letter-spacing: .25px;
                line-height: 20px;
                height: 32px;
                display: flex;
                align-items: center;
            }

            & .day-container {
                display:  grid;
                grid-template-columns: repeat(7, 1fr);
                
                & .day-w {
                    font-size: 12px;
                    font-weight: 400;
                    color: #70757a;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 28px;
                    max-width: 28px;
                }
            }
        }

        & .container {
            display: grid;
            grid-template-columns: repeat(7 ,1fr);
            
            & .date-w {
                display: flex;
                align-items: center;
                justify-content: center;
                color: #3c4043;
                font-size: 12px;
                font-weight: 400;
                max-width: 28px;
                height: 28px;
                cursor: pointer;
                border-radius: 50%;

                &.active {
                    background-color: #1a73e8;
                    color: #fff;
                }

                &:hover {
                    background-color: #f1f3f4;

                    &.no-hover {
                        background-color: #ffffff;
                    }

                    &.active {
                        background-color: #1967d2;
                    }
                }
            }
        }
    }

    //PRIMARY CALENDAR
    &.full-calendar {
        width: 100%;
        height: 100%;
        position: relative;

        //Day view
        &.day-view {
            position: relative;
            overflow: hidden;

            & .calendar-header {
                display: block;
                flex-direction: column;
                padding-bottom: 0;
                position: sticky;
                top: 0;

                & .day-container {
                    margin-left: 92.39px;
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    margin-bottom: 2px;

                    & span.day {
                        color: #1a73e8;
                        font-size: 11px;
                        font-weight: 500;
                        letter-spacing: 0.8px;
                        margin-left: 0;
                        margin-top: 8px;
                        text-indent: 0.8px;
                        text-transform: uppercase;
                        margin-bottom: 4px;
                        color: #70757a;
                    }
    
                    & div.date-head {
                        font-size: 25px;
                        letter-spacing: -2.5px;
                        text-indent: -2.5px;
                        font-variant: tabular-nums;
                        -webkit-font-feature-settings: "tnum" 1;
                        font-feature-settings: "tnum" 1;
                        width: 46px;
                        height: 46px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 50%;
                        color: #70757a;
                    }

                    &.active {
                        & div.date-head {
                            color: white;
                            background-color: #1a73e8;
                        }

                        & span.day {
                            color: #1a73e8;
                        }
                    }
                }

                & .event-today-container {
                    display: flex;
                    align-items: center;

                    & .gmt-container {
                        height: 24px;
                        padding: 0px 24px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-right: 1px solid $gray-2;

                        & span {
                            color: #70757a;
                            font-size: 10px;
                            -webkit-font-smoothing: subpixel-antialiased;
                        }
                    }

                    & .events-container {
                        display: flex;
                        flex: 1;
                        box-sizing: border-box;
                        flex-direction: column;
                    
                        & .event {
                            flex: 1;
                            height: 22px;
                            padding: 0 8px;
                            line-height: 20px;
                            color: #fff;
                            font-size: 12px;
                            font-weight: 500;
                            -webkit-border-radius: 4px;
                            border-radius: 4px;
                            -webkit-box-sizing: border-box;
                            cursor: pointer;
                            box-sizing: border-box;
                            pointer-events: auto;
                            overflow: hidden;
                            display: -webkit-box;
                            display: -webkit-flex;
                            display: flex;
                            outline: none;
                            -webkit-align-items: center;
                            align-items: center;
                            border: 1px solid transparent;
                            -webkit-transition: background-color 100ms linear;
                            transition: background-color 100ms linear;
                            margin-left: 4px;
                            margin-top: 4px;
    
                            & span.event-name {
    
                            }
                        }
                    }
                }
            }

            & .calendar-container {
                overflow: auto;
                width: 100%;
                display: flex;
                flex-direction: column;
                overflow: auto;
                position: absolute;
                top: 0;
                padding-top: 119px;
                height: 100%;
                box-sizing: border-box;

                & .time-row {
                    width: 100%;
                    display: flex;
                    align-items: center;

                    & .time-container {
                        height: 48px;
                        border-right: 1px solid $gray-2;
                        position: relative;
                        width: 87.39px;
                        display: flex;

                        & span {
                            color: #70757a;
                            font-size: 10px;
                            position: absolute;
                            top: -5px;
                            right: 20px;
                        }

                        &::after {
                            content: "";
                            display: block;
                            width: 11px;
                            height: 1px;
                            background-color: $gray-2;
                            position: absolute;
                            right: 0;
                            top: 0;
                        }
                    }

                    & .events-container {
                        height: 48px;
                        flex: 1;
                        border-top: 1px solid $gray-2;
                        display: flex;
                        box-sizing: border-box;
                        padding-right: 11px;
                        padding-left: 4px;
                        
                        & .event {
                            flex: 1;
                            height: 100%;
                            color: #fff;
                            border-radius: 4px;
                            display: flex;
                            flex-direction: column;
                            cursor: pointer;
                            
                            &.inline {
                                flex-direction: row;

                                & .event-name {
                                    margin-right: 0!important;
                                }
                            }

                            &.start {
                                border-bottom-right-radius: 0;
                                border-bottom-left-radius: 0;
                            }

                            &.mid {
                                border-radius: 0;
                                position: relative;

                                &::after {
                                    content: '';
                                    position: absolute;
                                    display: block;
                                    top: -1px;
                                    left: 0;
                                    width: 100%;
                                    height: calc(100% + 2px);
                                    z-index: 2;
                                    background-color: inherit;
                                }

                                & .event-name {
                                    opacity: 0;
                                }
                            }

                            &.end {
                                border-top-right-radius: 0;
                                border-top-left-radius: 0;

                                & .event-name {
                                    opacity: 0;
                                }
                            }

                            & .event-name {
                                font-size: 12px;
                                font-weight: 400;
                                letter-spacing: .1;
                                line-height: 15px;
                                color: #ffffff;
                                margin: 4px 8px -4px 8px;
                            }
                        }
                    }
                }
            }
        }

        //Week view
        &.week-view {
            & .calendar-header{
                & .day-group {
                    display: grid;
                    grid-template-columns: repeat(7, 1fr);
                    padding-left: 92.39px;
                    box-sizing: border-box;

                    & .day-container {
                        margin: 0;
                    }
                }

                & .event-today-container {
                    & .event-group {
                        flex: 1;
                        display: grid;
                        grid-template-columns: repeat(7, 1fr);
                        min-height: 24px;
    
                        & .events-container {
                            border-right: 1px solid $gray-2;
                            min-width: 0;
                            min-height: 0;
                            & .event {
                                min-height: 22px;
                                position: relative;
                                margin-right: 4px;
                                cursor: pointer;

                                & span.event-name { 
                                    overflow: hidden;
                                    text-overflow: ellipsis;
                                    display: -webkit-box;
                                    -webkit-line-clamp: 1; /* number of lines to show */
                                    -webkit-box-orient: vertical;
                                    white-space: pre-wrap;
                                }

                                &::after {
                                    content: '';
                                    display: block;
                                    position: absolute;
                                    height: 100%;
                                    background-color: inherit;
                                    width: 100%;
                                    z-index: -1;
                                }
                                
                                &.start{
                                    margin-right: 0;
                                    border-top-right-radius: 0;
                                    border-bottom-right-radius: 0;

                                    &::after {
                                        right: 0;
                                    }
                                }
                                
                                &.mid{
                                    margin-right: 0;
                                    margin-left: 0;
                                    border-radius: 0;

                                    & span.event-name {
                                        opacity: 0;
                                        display: none;
                                    }

                                    &::after {
                                        right: 0;
                                    }
                                }
                                
                                &.end{
                                    margin-left: 0;
                                    border-top-left-radius: 0;
                                    border-bottom-left-radius: 0;

                                    & span.event-name {
                                        opacity: 0;
                                        display: none;
                                    }

                                    &::after {
                                        left: 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }

            & .calendar-container {
                padding-top:112px;
                
                &::-webkit-scrollbar {
                    display: none;
                }

                & .event-group {
                    flex: 1;
                    display: grid;
                    min-height: 24px;
                    box-sizing: border-box;
                    grid-template-columns: repeat(7, 1fr);
                    //border-bottom: 1px solid $gray-2;
                    //padding-left: 4px;
                
                    & .events-container {
                        border-right: 1px solid $gray-2;
                        min-width: 0;
                        min-height: 0;
                        //border-top: none;
                        //border-bottom: none;
                        padding-right: 0!important;
                        padding-left: 0!important;

                        & .event {
                            min-width: 0;
                        }

                        & .event.mid, .event.end {
                            & span {
                                display: none;
                            }
                        }
                    }
                }
            }
        }

        //Year view
        &.year-view {
            & .calendar-container {
                display: grid;
                grid-template-columns: repeat(4, 1fr);
                grid-gap: 40px;
                padding: 40px;
                box-sizing: border-box;
                overflow: auto;
                position: absolute;
                top: 0;
                left: 0;
            }
        }

        & .date {    
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            white-space: nowrap;
            font-size: 13px;
            font-weight: 500;
            cursor: pointer;
            border-right: 1px solid $gray-2;
            border-bottom: 1px solid $gray-2;
            color: $dark;
            position: relative;

            & .event-widget {
                flex: 1;
                width: 100%;

                &.small {
                    flex: unset;
                    height: 24px;
                    border-radius: 8px;
                    margin-top: 8px;
                }
            }

            &.active {
                color: #fff!important;
                z-index: 2;

                & span {
                    width: 24px;
                    height: 24px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    background: $blue;
                    color: #fff!important;
                }
            }

            &.focused {
                color:$blue!important;
                z-index: 2;

                & .date-name {
                    width: 24px;
                    height: 24px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    background: $blue-second;
                }
            }
        }

        & .calendar-header {
            display: grid;
            width: 100%;
            grid-template-columns: repeat(7,1fr);
            border-bottom: 1px solid $gray-2;
            padding: 16px 0;
            position: sticky;
            background-color: #fff;
            top: 0;
            z-index: 99999;

            & .date {
                border: none;
                color: #70757a;
                font-size: 11px;
                font-weight: 400;
                line-height: 20px;
            }
        }

        & .calendar-container {    
            display: grid;
            grid-template-columns: repeat(7,1fr);
            width: 100%;
            height: 100%;

            & .date {
                box-sizing: border-box;
                padding: 13px;
                min-width: 0;

                & .date-name {
                    width: 24px;
                    height: 24px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    color: $dark;
                    font-size: 12px;
                    font-weight: 400;
                    letter-spacing: .3px;
                }
            }
        }
    }

    //EVENTS
    &.event-widget {
        border-radius: 13px;
        padding: 13px 13px 33px 13px;
        box-sizing: border-box;
        position: relative;
        z-index: 2;
        margin-top: 8px;

        &.label-visible {
            & .time {
                display: block!important;
                background-color: transparent!important;
            }

            & .name {
                display: block!important;
            }
        }

        &.start {
            border-top-right-radius: 0!important;
            border-bottom-right-radius: 0!important;

            &::after {
                content: "";
                display: block;
                width: calc(100% + 1px);
                height: 100%;
                position: absolute;
                background-color: inherit;
                z-index: -1;
            }
        }

        &.mid {
            border-radius: 0!important;

            & .time {
                display: none;
            }

            & .name {
                display: none
            }

            &::after {
                content: "";
                display: block;
                width: calc(100% + 28px);
                height: 100%;
                position: absolute;
                position: absolute;
                background-color: inherit;
                z-index: -1;
                left: -14px;
            }
        }

        &.end {
            border-top-left-radius: 0!important;
            border-bottom-left-radius: 0!important;

            & .time {
                display: none;
            }

            & .name {
                display: none
            }

            &::after {
                content: "";
                display: block;
                width: calc(100% + 1px);
                height: 100%;
                background-color: inherit;
                position: absolute;
                z-index: -1;
                left: -14px;
            }
        }

        &.small {
            display: flex;
            align-items: center;
            padding: 13px;

            & p.name {
                margin: 0;
                margin-left: 8px;
                flex: 1;
                -webkit-line-clamp: 1; /* number of lines to show */
            }

            & .avatar {
                display: none;
            }
        }

        & span.time {
            font-size:  12px;
            color: #fff;
            font-weight: 500;
            background-color: transparent!important;
        }

        & p.name {
            font-size:  12px;
            color: #fff;
            font-weight: 500;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 4; /* number of lines to show */
            -webkit-box-orient: vertical;
        }

        & .avatar {
            background-color: $gray;
            border-radius: 50%;
            width: 20px;
            height: 20px;
            border: 1px solid #fff;
            position: absolute;
            bottom: 13px;
            left: 13px;
        }
    }

    &.controller {
        & .header {
            display: flex;
            align-items: center;
            width: 100%;
            margin-bottom: 16px;

            & .title {
                font-size: 14px;
                color: $dark;
                flex: 1;
                font-weight: 500;
                letter-spacing: .25px;
                line-height: 20px;
            }

            & .action {
                display: flex;
                align-items: center;
                justify-content: center;

                & .icon {
                    font-size: 18px;
                    color: $dark;
                    cursor: pointer;
                }
            }
        }

        & .calendar-container {
            & .date {
                width: 100%;
                height: 30px;
                display: flex;
                align-items: center;
                justify-content: center;
                white-space: nowrap;
                cursor: pointer;
                font-size: 10px;
                color: $dark;
                position: relative;
                
                &.active {
                    color: #fff!important;
                    z-index: 2;

                    &::after {
                        content: '';
                        display: block;
                        position: absolute;
                        width: 24px;
                        height:  24px;
                        border-radius: 50%;
                        background: $blue;
                        z-index: -1;
                    }
                }

                &.focused {
                    color: $blue!important;
                    z-index: 2;

                    &::after {
                        content: '';
                        display: block;
                        position: absolute;
                        width: 24px;
                        height:  24px;
                        border-radius: 50%;
                        background: $blue-second;
                        z-index: -1;
                    }
                }
            }

            & .calendar-header {
                display: grid;
                width: 100%;
                grid-column-gap: 8px;
                grid-template-columns: repeat(7,1fr);

                & .date {
                    color: $dark-2;
                    font-size: 10px;
                    font-weight: 500;
                    color: #70757a;
                }
            }

            & .calendar-dates {
                display: grid;
                grid-column-gap: 8px;
                grid-template-columns: repeat(7,1fr);
                width: 100%;
            
                & .date {
                    color: $dark;
                }
            }
        }
    }
}

.popup {
    position: relative;

    &.year-calendar-event {
        width: 244px;
        box-shadow: 0 1px 3px 0 rgba(60, 64,67, .3), 0 4px 8px 3px rgba(60, 64, 67, .15);
        background-color: #fff;
        border-radius: 8px;
        overflow: hidden;
        position: absolute;
        padding: 13px;
        box-sizing: border-box;
        min-height: 48px;
        z-index: 999;

        & .date-container {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            & .day-span {
                color: #70757a;
                font-size: 11px;
                letter-spacing: .8px;
                line-height: 16px;
            }

            & .date-span {
                color: #80868b;
                font-weight: 400;
                font-size: 26px;
                letter-spacing: -2.6px;
                text-indent: -2.6px;
                font-variant: tabular-nums;
                font-feature-settings: "tnum" 1;
                line-height: 40px;
                width: 40px;
                -webkit-border-radius: 50%;
                border-radius: 50%;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        & .events-container {
            display: flex;
            flex-direction: column;
            justify-content: center;

            & .event-allday {
                width: 100%;
                height: 22px;
                padding: 0 8px;
                box-sizing: border-box;
                margin-top: 2px;
                cursor: pointer;
                border-radius:  4px;
                position: relative;
                display: flex;
                align-items: center;

                &:hover {
                    opacity: .75;
                }

                &::after {
                    content: '';
                    display: block;
                    background-color: inherit;
                    position: absolute;
                    left: -7px;
                    width: 16px;
                    height:  16px;
                    transform: rotate(45deg);
                    z-index: -1;
                }

                &.start {
                    border-top-right-radius: 0;
                    border-bottom-right-radius: 0;
                    width: calc(100% - 11px);

                    &::after {
                        right: -9px;
                        left: unset;
                    }
                }

                &.mid {
                    border-radius: 0;
                    width: calc(100% - 22px);
                    margin-left: 11px;

                    &::after {
                        right: -9px;
                        left: unset;
                    }

                    &::before {
                        content: '';
                        display: block;
                        background-color: inherit;
                        position: absolute;
                        left: -7px;
                        right: unset;
                        width: 16px;
                        height:  16px;
                        transform: rotate(45deg);
                        z-index: -1;
                    }
                }

                &.end {
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                    width: calc(100% - 11px);
                    margin-left: 11px;

                    &::after {
                        left: -9px;
                        right: unset;
                    }
                }

                & .title {
                    line-height: 20px;
                    color: #fff;
                    font-size: 12px;
                }
            }

            & .event-today {
                width: 100%;
                height: 22px;
                padding: 0 8px;
                box-sizing: border-box;
                margin-top: 2px;
                display: flex;
                align-items: center;
                cursor: pointer;
                border-radius:  4px;

                & .dot {
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    margin-right: 4px;
                }

                & .time {
                    color: #3c4043;
                    font-size: 12px;
                    margin-right: 4px;
                }

                & .title {
                    color: #3c4043;
                    line-height: 20px;
                    font-size: 12px;
                    font-weight: 500;
                }

                &:hover {
                    background-color:  $gray;
                }
            }
        }
    }

    & .close-btn {
        position: absolute;
        right: 3px;
        top: 3px;
        cursor: pointer;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        & .icon {
            color: #3c4043;
            width: 20px;
            height: 20px;
        }

        &:hover {
            background-color: $gray;
        }
    }
}