@import "../../styles/lavenst.scss";

.view {
    flex: 1;
    width: 100%;
    height: 100%;
    overflow: auto;
    box-sizing: border-box;
    position: relative;

    &.flex {
        display: flex;
    }

    &.header-bar {
        padding-top: $headerBarHeight;
    }

    &.no-overflow {
        overflow: hidden;
    }
}