@import "../../styles/lavenst.scss";

.checkbox {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    margin: 8px 0;

    &:first-child {
        margin-top: 0;
    }

    & .box {
    width: 14px;
    height: 14px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border: 2px solid #1a73e8;
    cursor: pointer;
    transition: all .25s ease;
    user-select: none;
    flex-shrink: 0;

    & .checkmark {
        width: 100%;
        height: 100%;
        visibility: hidden;
        opacity: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        transform: scale(.1);
        transition: all .25s ease;

        & img {
            width:  9.5px;
            height: 9.5px;
            margin-right: 1px;
            margin-top: 1px;
        }
    }

    &.checked {
        background-color: $primary-color;
        border-color:   $primary-border-color;

        & .checkmark {
            opacity: 1;
            visibility: visible;
            transform: scale(1);
        }
    }

    &.disabled {
        background-color: $disabled-background-color;
        border-color: $disabled-border-color;

        &.checked {
            background-color: $disabled-checked-background-color;
            border-color: $disabled-checked-border-color;
        }
    }
    }

    & .label {
        color: $dark;
        font-weight: 400;
        font-size: 13px;
        margin-left: 10px;
    }

    &:active {
        & .box {
            background-color: $gray-3;
            border-color: $gray-2;
            opacity: $active-opacity;
    
            &.checked {
                background-color: $primary-color;
                border-color:   $primary-border-color;
            }
        }

        & .label {
            opacity: $active-opacity;
        }
    }

    &.disabled {
        pointer-events: none;
        
        & .label {
            color: $disabled-font-color;
        }

        &.checked {
            & .label {
                color: $disabled-checked-font-color;
            }
        }
    }
}