@import "../../styles/lavenst.scss";

.sidebar {
    width: 300px;
    height: 100vh;
    display: inline-flex;
    border-right: 1px solid $gray-2;
    flex-direction: column;
    padding: 24px;
    box-sizing: border-box;
    position:relative;
    transition: all .4s ease;

    //MINIZED
    &.minized {
        width: 64px;
        padding: 24px 0;
        align-items: center;

        & .user-container {
            align-items: center;
            justify-content: center;

            & .avatar {
                display: none;
            }

            & span.username {
                display: none;
            }
            & .icon {
                position: relative;
            }
        }

        & ul.list {

        }

        & .sidebar-footer {
            width: 100%;
            position: absolute;
        }

        & .item {
            //width: 64px;
            padding: 10px 0;
            display: inline-flex;
            align-items: center;
            justify-content: center;

            & a {
                text-align: center;

                & span {
                    display: none;
                }

                & .icon {
                    margin: 0;
                }
            }
        }
    }

    & .user-container {
        width: 100%;
        height: 52px;
        display: flex;
        align-items: center;
        position:  relative;

        & .avatar {
            width: 44px;
            height: 44px;
            border-radius: 12px;
            background-color: #CCCCCC;
        }

        & span.username {
            color: $dark;
            font-weight: bold;
            font-size: 14px;
            margin-left: 16px;
        }

        & .icon {
            font-size: 24px;
            position: absolute;
            right: 0;
            cursor: pointer;
        }
    }

    & ul.list {
        list-style-type: none;
        width: 100%;
        padding: 0;
        margin: 0;
        margin-top: 32px;
    }

    & .sidebar-footer {
        width: calc(100% - 48px);
        position: absolute;
        bottom: 24px;
    }

    & .item {
        width: 100%;
        height: 34px;
        display: flex;
        align-items: center;
        padding: 10px 12px;
        cursor: pointer;
        position: relative;

        & a {
            text-decoration: none;
            font-size: 13px;
            color: $dark-2;
            display: flex;
            align-items: center;
            font-weight: 500;

            & .icon {
                margin-right: 20px;
                font-size: 20px;
            }
        }

        &.active {
            & a {
                color: $blue;
            }

            &::after {
                content: '';
                display: block;
                position: absolute;
                width: 3px;
                height: 34px;
                left: 0;
                background-color: $blue;
                border-radius: 2px;
            }
        }
    }
}

.sidebar-container {
    height: 100%;
    width: 300px;
    border-right: 1px solid $gray-2;
    display: flex;
    flex-direction: column;
    padding: 24px;
    box-sizing: border-box;
    position:relative;
    transition: all .4s ease;
    overflow: auto;
}

//HEADER
.sidebar-header {
    font-size: 13px;
    color: $dark;
    font-weight: 600;
    text-align:left;
    margin-bottom: 16px;
}