@import "../../styles/lavenst.scss";

.alert-container {
    &.modal {
        position: fixed;
        top: 0;
        left: 0;
        background:  rgba(0,0,0,0.502);
        width: 100vw;
        height: 100vh;
        z-index: 99999999;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        visibility: hidden;

        &.visible {
            opacity: 1;
            visibility: visible;
        }

        & .alert {
            box-shadow: 0 1px 3px 0 rgba(60,64,67,.3), 0 4px 8px 3px rgba(60, 64, 67, .15);
            background-color: #fff;
            border-radius: 8px;
            max-width: 24em;
            position: relative;
            display: flex;
            flex-direction: column;

            & .content {
                padding: 18px 24px 16px 24px;
                box-sizing: border-box;

                & .title {
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: .1px;
                    line-height: 24px;
                }
            }

            & .action-container {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                padding: 16px 8px 8px 24px;
                box-sizing: border-box;
                cursor: pointer;

                & .action-btn {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 36px;
                    font-size: 14px;
                    font-weight: 500;
                    letter-spacing: .25px;
                    line-height: 36px;
                    color: #5f6368;
                    padding: 0 8px;
                    
                    &.recommend {
                        color: #1a73e8;
                    }
                }
            }
        }
    }
}