select {
    outline: none;
    background: transparent;
    border: none;
    border-radius: 8px;
    height: 32px;
    box-sizing: border-box;
    padding: .375rem .75rem!important;
    font-weight: 500;
    transition: all .15s ease!important;
    font-weight: 500;
    background-color: $gray;

    &.large {
        height: 36px;
    }
}