@import '../../styles/lavenst.scss';

.headerbar {
    width: 100%;
    height: 64px;
    border-bottom: 1px solid $gray-2;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;

    & .left {
        flex: 1;
        display: flex;
        padding-left:24px;
        box-sizing: border-box;
        align-items: center;
    }

    & .center {
        flex: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        align-items: center;
    }

    & .right {
        flex: 1;
        display: flex;
        flex-direction:row-reverse;
        padding-right:24px;
        box-sizing: border-box;
        align-items: center;
    }

    & .searchbar {
        height: 46px;
        width: 100%;
        max-width: 722px;
        box-sizing: border-box;
        padding: 0 13px 0 4px;
        border-radius: 7px;
        background-color: $gray;
        display: flex;
        align-items: center;

        & .icon {
            color: #5f6368;
            padding: 8px;
            margin: 3px;
            width: 18px;
            height: 18px;
        }

        & input {
            flex: 1;
        }
    }

    & button {
        margin: 0 16px;

        // &:first-child {
        //     margin: 0;
        // }
    }

    & h2.header {
        font-size: 20px;
        color: #3c4043;
        font-weight: 400;
        margin-left: 8px;
    }

    & .back-btn {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        & .icon {
            width: 24px;
            height: 24px;
            color: #5f6368;
        }

        &:hover {
            background-color: $gray;
        }
    }

    & .search-btn {
        width: 36px;
        height: 36px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 8px;
        cursor: pointer;

        & .icon {
            font-size: 18px;
            color: #5f6368;
        }

        &:hover {
            background-color: $gray;
        }
    }

    & .goback {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        & .icon {
            font-size: 20px;
            color:  $blue;
        }

        & span {
            font-size: 16px;
            color: $blue;
            font-weight: 500;
            margin-left: 8px;
        }
    }

    & .calendar {
        display: flex;
        align-items: center;
        justify-content: center;

        & .date {
            font-size: 22px;
            font-weight: 400;
            letter-spacing: 0;
            line-height: 28px;
            white-space: nowrap;
            color: $dark;
            margin-left: 17px;
            margin-right: 17px;
        }

        & .action {
            display: flex;
            align-items: center;
            justify-content: center;

            & .action-btn{
                width: 32px;
                height: 32px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;

                & .icon {
                    cursor:pointer;
                    font-size: 18px;
                }

                &:hover {
                    background-color: $gray;
                }
            }

            & button {

            }
        }
    }
}