@import "../../styles/lavenst.scss";

.select {
    //min-width: 193px;
    cursor: pointer;
    user-select: none;
    display: inline-flex;
    flex-direction: column;
    position:  relative;

    & .box {
        width: 100%;
        height: 36px;
        border-radius: 4px;
        background-color: #f1f3f4;
        display: flex;
        align-items: center;

        & .label {
            display: block;
            font-size: 14px;
            font-weight: 400;
            color: $dark;
            margin-left: 8px;
            margin-bottom: 3px;
            flex: 1;

            &.color {
                display: flex;
                align-items: center;

                & .color {
                    border-radius: 50%;
                    box-sizing: border-box;
                    display: inline-block;
                    outline: none;
                    width: 18px;
                    height: 18px;
                    margin: 3px;
                    flex-shrink: 0;
                    cursor: pointer;
                    position: relative;
                }
            }
        }

        & .mark {
            margin-right: 4px;
            width: 18px;
            height: 18px;
            border-radius: 4px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            background-color: #f1f3f4;
            margin-left: 4px;

            & img {
                will-change: transform;
                transform: rotate(0deg);
                margin-top: 1px;
                transition: transform .4s ease;
            }

            & .icon {
                color: #5f6368;
            }
        }
    }

    & .option-container {
        will-change: transform;
        position: absolute;
        list-style-type: none;
        padding: 6px 0;
        margin: 0;
        min-width:  100%;
        box-sizing: border-box;
        background-color: #fff;
        margin-top: 4px;
        opacity: 0;
        z-index: 999999;
        visibility: hidden;
        transition: all .4s ease;
        //transform: translateY(-64px);
        box-shadow: 0px 8px 10px 1px rgba(0, 0, 0, .14), 0px 3px 14px 2px rgba(0, 0, 0, .12), 0px 5px 5px -3px rgba(0, 0, 0, .2);

        & .item {
            font-size: 14px;
            font-weight: 400;
            color: $dark;
            padding: 8px;
            box-sizing: border-box;
            min-height: 36px;
            white-space: nowrap;

            &:hover {
                background-color: #eeeeee;
            }
        }

        &.color {
            display: grid;
            grid-template-columns: repeat(2, 1fr);

            & .item {
                display: flex;
                align-items: center;
                justify-content: center;

                & .color {
                    border-radius: 50%;
                    box-sizing: border-box;
                    display: inline-block;
                    outline: none;
                    width: 18px;
                    height: 18px;
                    margin: 3px;
                    cursor: pointer;
                    position: relative;
                }
            }
        }
    }

    &.visible {
        & .box {
            border-color: #f1f3f4;

            & .mark {
                & img {
                    transform: rotate(180deg);
                    margin: 0;
                }
            }
        }

        & .option-container {
            opacity: 1;
            visibility: visible;
            //transform: translateY(0px);
        }
    }

    &.disabled {
        pointer-events: none;

        & .box  {
            & .label {
                color: $dark;
            }

            & .mark {
                background-color: #f1f3f4;
            }
        }
    }
}