.btn {
    outline: none;
    background: transparent;
    border: none;
    border-radius: 7px;
    padding: 8px 13px;
    background-color: $blue;
    box-shadow: $blue-shadow;
    color: #fff;
    font-weight: 500;
    font-size: 13px;
    height: 36px;
    display: flex;
    align-items: center;
    white-space: nowrap;
    cursor: pointer;
    font-family: $default-font;

    &.nolabel .icon {
        margin-right: 0;
    }

    & .icon {
        font-size:  15px;
        margin-right:8px;
    }

    &.second {
        padding: 8px;
        color: $blue;
        background-color: $blue-second;
        box-shadow: none;

        & .icon {
            font-size: 20px;
        }
    }

    &.third {
        background-color: $gray;
        box-shadow: none;
        color: $dark-2;
    }

    &.outline {
        box-shadow: none;
        color: $dark-2;
        border: 1px solid #dadce0;
        background-color:#fff;
        border-radius: 4px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        height: 36px;
        font-size: 13px;
        font-weight: 500;
        letter-spacing: .25px;
        text-transform: none;

        &:hover  {
            background-color: #f1f3f4;
            border: 1px solid #dadce0;
        }
    }
}