@import "../../styles/lavenst.scss";

.headbreak {
    width:  100%;
    border-bottom: 1px solid  $gray-2;
    margin: 28px 0 14px 0;

    & .container {
        position: relative;
        height: 40px;
        display: inline-flex;
        align-items: center;
        justify-content: center;

        & .title {
            color: #1a73e8;
            font-size: 14px;
            font-weight: 500;
            letter-spacing: .25px;
            text-transform: none;
        }

        & .line {
            left: 0px;
            width: 100%;
            background-color: #4285f4;
            bottom: 0;
            height: 2px;
            position: absolute;
            border-radius: 3px 3px 0 0;
            height: 3px;
        }
    }
}