@import "../../styles/lavenst.scss";

.action-btn-create-calendar {
    border-radius: 24px;
    height: 48px;
    margin-top: 4px;
    width: auto;
    border: 0;
    font-weight: 500;
    padding: 0;
    color: $dark;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: .25px;
    text-transform: none;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, .14), 0px 2px 1px -1px rgba(0, 0, 0, .12), 0px 1px 3px 0px rgba(0, 0, 0, .2);
    background-color: #fff;
    cursor: pointer;
    margin-bottom: 28px;

    & svg {
        margin-right: 8px;
    }
}