@import "../../styles/lavenst.scss";

.textbox {
    width: 100%;
    display: inline-flex;
    margin-top: 16px;
    outline: none;
    border: none;
    background-color: #f1f3f4;
    border-radius: 4px;
    box-sizing: border-box;
    height: 36px;
    align-items: center;
    justify-content: center;
    padding: 0 8px;
    font-family: $default-font;
    position: relative;
    font-weight: 400;

    &::placeholder {
        font-weight: 400;
    }

    &::after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        width: 0;
        height: 2px;
        background-color: #4285f4;
        transition: all .25s ease;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
    }

    &:focus-within {
        &::after {
            width: 100%;
        }
    }

    &.underline {
        background-color: transparent;
        border-radius: 0;
        height: 48px;
        border-bottom: 1px solid $gray-2;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        padding-left: 0;
        padding-right: 0;

        & input {
            font-size: 28px;
        }

        &::after {
            content: '';
            display: block;
            position: absolute;
            bottom: 0;
            width: 0;
            height: 2px;
            background-color: #4285f4;
            transition: all .25s ease;
        }

        &:focus-within {
            &::after {
                width: 100%;
            }
        }
    }

    & span.label {
        margin-bottom: 4px;
        font-size: 12.5px;
        font-weight: 600;
        color: $dark-2;
    }

    & input {
        width:  100%;
        outline: none;
        background: transparent;
        border: none;
    }

    & textarea {
        width: 100%;
        height: 100%;
        outline: none;
        background: transparent;
        border: none;
        resize: none;
        font-size: 14px;
    }

    &.textarea {
        height: 180px;
        padding-top: 8px;
        font-family: $default-font;
        position: relative;
        border: 0;
        outline: 0;
        display: block;

        &.div:focus-within {
            &::after {
                width: 0;
            }
        }
    }

    & .icon {
        &.right {
            
        }
    }
}