@import "../../styles/lavenst.scss";

.laven.date_picker {
    position: relative;
    width: 266px;
    flex: 1;

    & input {
        width: 100%;
        height: unset!important;
    }

    & .date_picker_popup {
        padding: 8px 8px;
        z-index: 9999999999;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0 2px 6px rgba(0,0,0,.2);
        position: absolute;
        left: 0;
        margin-top: 13px;

        & .control_container {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 16px 0;
            position: relative;

            & button.control {
                width: 32px;
                height: 32px;
                border-radius: 8px;
                box-shadow: 0 2px 6px rgba(0,0,0,.2);
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                position: absolute;
                padding: 0;
                background-color: #fff;
                outline: none;
                border: none;

                &.prev {
                    left: 8px;
                }

                &.next {
                    right: 8px;
                }

                & .icon {
                    font-size: 16px;
                    color: #414141;
                    margin: 0;
                }
            }

            & span.date {
                margin: 0 4px;
                cursor: pointer;
                font-weight: bold;
                color: #888;

                &.active {
                    color: #000;
                }
            }
        }

        & .calendar_container {
            margin-top: 8px;

            & .date {
                width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                white-space: nowrap;
                font-size: 14px;
                font-weight: 500;
                cursor: pointer;

                &.active {
                    background-color: rgba(36,131,255,.18);
                    border-radius: 8px;
                    color: #2484FF;
                }

                &.focused {
                    background-color: #2484FF;
                    border-radius: 8px;
                    color: #fff;
                    box-shadow: 0 1px 3px rgba(0,0,0,.2);
                }
            }

            & .calendar_header {
                display: grid;
                width: 100%;
                grid-column-gap: 8px;
                grid-template-columns: repeat(7, 1fr);

                & .date {
                    color: #888;
                }
            }

            & .calendar_dates {
                display:grid;
                grid-column-gap: 8px;
                grid-template-columns: repeat(7, 1fr);
                width: 100%;
            }
        }

        & .month_container {
            margin-top: 8px;
            width: 272px;

            & .calendar_months {
                display: grid;
                grid-column-gap: 8px;
                grid-template-columns: repeat(3, 1fr);
                width: 100%;
                padding-bottom: 8px;

                & .month {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 64px;
                    cursor: pointer;

                    &.active {
                        background-color: rgba(36,131,255,.18);
                        border-radius: 8px;
                        color: #2484FF;
                    }
    
                    &.focused {
                        background-color: #2484FF;
                        border-radius: 8px;
                        color: #fff;
                        box-shadow: 0 1px 3px rgba(0,0,0,.2);
                    }
                }
            }
        }

        & .year_container {
            margin-top: 8px;
            width: 272px;
            overflow: auto;
            max-height: 340px;

            & .calendar_years {
                display: grid;
                grid-column-gap: 8px;
                grid-template-columns: repeat(3, 1fr);
                width: 100%;
                padding-bottom: 8px;

                & .year {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 64px;
                    cursor: pointer;

                    &.active {
                        background-color: rgba(36,131,255,.18);
                        border-radius: 8px;
                        color: #2484FF;
                    }
    
                    &.focused {
                        background-color: #2484FF;
                        border-radius: 8px;
                        color: #fff;
                        box-shadow: 0 1px 3px rgba(0,0,0,.2);
                    }
                }
            }
        }
    }
}